body {
  background-color: #22254b;
  color:white;
  font-family:"poppins","sans-serif";
  margin:0;
}
.root{
  display: flex;
  flex-direction: column;
  
}

header{
  background-color: #373b69;
  color:white;
  display: flex;
  flex-direction: row;
  padding: 1rem;
  justify-content: flex-end;
  align-items: center;
}

.search{
  background-color: transparent;
  border: 2px solid #22254b;
  font-family: inheirt;
  color:white;
  font-size:1.2rem;
  border-radius: 50px;
  padding: 0.5rem 1.5rem;

}

.text{
  text-align: center;
}



.search:focus{
  outline:0;

}

.movie{
  background-color: #373b69;
  margin:1rem;
  width:300px;
  border-radius: 3px;
  box-shadow:3px 3px 5px rgba(0,0,0,0.1) ;
  overflow:hidden;
  position: relative;
}
.movie-container{
  display: flex;
  justify-content: center;
  flex-wrap:wrap;
}

.movie img{
  object-fit: cover;
  height:450px;
  width: 100%;

}

.movie-info{
  display:flex;
  padding:1rem;
  justify-content: space-between;
  align-items: center;
}

.movie-info h3{
  margin:0;
}

.movie-over{
  background-color: #fff;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  box-shadow:0 -3px 5px rgba(0,0,0,0.1) ;
  color: #22254b;
  padding:1rem;
  position:absolute;
  bottom:0;
  left:0;
  right:0;
transform: translateY(102%);
transition: transform 0.3s ease-in-out;
overflow: auto;
max-height: 100%;
}

.tag{
  background-color: #22254b;
  border-radius: 5px;
  padding:0.5rem;
  font-weight:bold;
}

.tag.green{
  color:rgb(5, 184, 5);
}

.tag.orange{
  color:orange;
}

.tag.red{
  color:red;
}

.movie:hover .movie-over{
  transform: translateY(0%);
}


.whole {
  width: 300px;
  transform: translateX(15px);
 }

